@font-face {
    font-family: Raleway-Light;
    font-style: normal;
    font-weight: 300;
    font-display: swap;
    src: local("Raleway-Light"), url("./../fonts/Raleway-Light.woff2") format("woff2"), url("./../fonts/Raleway-Light.woff") format("woff");
}

@font-face {
    font-family: Raleway-Regular;
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Raleway-Regular"), url("./../fonts/Raleway-Regular.woff2") format("woff2"), url("./../fonts/Raleway-Regular.woff") format("woff");
}

@font-face {
    font-family: Raleway-Medium;
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local("Raleway-Medium"), url("./../fonts/Raleway-Medium.woff2") format("woff2"), url("./../fonts/Raleway-Medium.woff") format("woff");
}

@font-face {
    font-family: Raleway-SemiBold;
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: local("Raleway-SemiBold"), url("./../fonts/Raleway-SemiBold.woff2") format("woff2"), url("./../fonts/Raleway-SemiBold.woff") format("woff");
}

@font-face {
    font-family: Raleway-SemiBold;
    font-style: normal;
    font-weight: 600;
    font-display: swap;
    src: local("Raleway-SemiBold"), url("./../fonts/Raleway-SemiBold.woff2") format("woff2"), url("./../fonts/Raleway-SemiBold.woff") format("woff");
}

@font-face {
    font-family: Marmelad;
    font-style: normal;
    font-weight: 400;
    font-display: swap;
    src: local("Marmelad"), url("./../fonts/marmelad.woff2") format("woff2"), url("./../fonts/marmelad.woff") format("woff");
}

@font-face {
    font-family: Roboto-Medium;
    font-style: normal;
    font-weight: 500;
    font-display: swap;
    src: local("Roboto-Medium"), url("./../fonts/Robotomedium.woff2") format("woff2"), url("./../fonts/Robotomedium.woff") format("woff");
}

* {
    padding: 0px;
    margin: 0px;
    border: none;
}

*,
*::before,
*::after {
    box-sizing: border-box;
}

aside,
nav,
footer,
header,
section,
main,
img,
svg {
    display: block;
}

ul,
ul li {
    list-style: none;
}

img {
    vertical-align: top;
}

img,
svg {
    max-width: 100%;
    height: auto;
}

a {
    text-decoration: none;

    a:hover,
    a:focus {
        outline: none;
    }
}

button {
    display: block;
    background: transparent;
}

input:focus,
input:active,
button:focus,
button:active {
    outline: none;
}

legend {
    display: block;
}

html {
    height: 100%;
}

.page {
    font-family: Raleway-Regular, Arial, Helvetica, sans-serif;
    font-size: 16px;
    line-height: 19px;
    color: #372e11;
    background: #fffbed;

    @media (min-width:576px) {
        font-size: 18px;
        line-height: 21px;
    }
}

h1 {
    font-family: Marmelad, Arial, Helvetica, sans-serif;
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 24px;

    span {
        display: none;
    }

    @media (min-width:576px) {
        font-size: 56px;
        line-height: 67px;
        margin-bottom: 40px;

        span {
            display: inline;
        }
    }
}

h2 {
    font-family: Marmelad, Arial, Helvetica, sans-serif;
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 32px;

    @media (min-width:576px) {
        font-size: 36px;
        line-height: 43px;
        margin-bottom: 42px;
    }
}

.link {
    color: #372e11;

    &:hover,
    &:focus {
        color: #372e11;
        outline: none;
    }
}

.link_color_yellow {
    color: #fffbed;

    &:hover,
    &focus {
        color: #fffbed;
    }
}

.link_size_l {
    font-size: 20px;
    line-height: 23px;
}

.link_size_x {
    font-size: 16px;
    line-height: 19px;

    @media (min-width:576px) {
        font-size: 20px;
        line-height: 23px;
    }
}

.link_semibold {
    font-family: Raleway-Medium, Arial, Helvetica, sans-serif;
}

.button {
    border-radius: 12px;
    text-align: center;
    cursor: pointer;
    font-size: 18px;
    line-height: 21px;
    color: #372e11;

    &:hover,
    &:focus {
        color: #372e11;
        outline: none;
    }
}

.button_bold {
    font-family: Raleway-SemiBold, Arial, Helvetica, sans-serif;
}

.button_semibold {
    font-family: Raleway-Medium, Arial, Helvetica, sans-serif;
}

.button_size_s {
    font-size: 16px;
    line-height: 19px;
}

.button_size_x {
    font-size: 16px;
    line-height: 19px;

    @media (min-width:576px) {
        font-size: 18px;
        line-height: 21px;
    }
}

.button_theme_yellow {
    background: #ffe182;
}

.button_theme_gray {
    background: #ededed;
}

.button_theme_block {
    width: 100%;
}

.button_theme_full {
    flex: 1 1 auto;

    @media (min-width:992px) {
        padding-left: 40px;
        padding-right: 40px;
    }
}

.checkbox {
    margin: 0;
    position: relative;
    cursor: pointer;
    font-size: 14px;
    line-height: 18px;

    & label {
        padding: 0 0 0 35px;
        position: relative;
        display: block;
        cursor: pointer;

        &:after {
            content: "";
            position: absolute;
            display: block;
            width: 20px;
            height: 20px;
            background-color: #fff;
            border: 1px solid #0e493f;
            border-radius: 4px;
            left: 0;
            top: 2px;
            margin-right: 5px;
        }
    }

    & input[type=checkbox] {
        top: 0;
        position: absolute;
        opacity: 0;
        clip: rect(0, 0, 0, 0);

        &:checked+label:after,
        &:checked+label:hover:after {
            border-color: #0e493f;
            background: #0e493f url("./../svg/checkbox.svg") center no-repeat;
        }
    }

    &.disabled {
        opacity: 0.4;
        cursor: default;

        label {
            cursor: default;
        }
    }
}

.header {
    position: relative;
    padding: 8px 0;

    @media (min-width:768px) {
        padding: 20px 0;
    }

    @media (min-width:992px) {

        &:after {
            content: "";
            display: block;
            position: absolute;
            width: 68px;
            height: 48px;
            background: url("./../images/header-bg.png") center no-repeat;
            left: 50%;
            top: 0;
            margin-left: 28px;
            z-index: -1;
        }
    }
}

.logo {
    width: 48px;

    @media (min-width:576px) {
        width: 98px;
    }
}

.header-container {
    display: flex;
    justify-content: space-between;
    align-items: center;
    padding: 0 8px;

    @media (min-width:360px) {
        padding: 0 20px;
    }

    @media (min-width:1460px) {
        margin: 0 auto;
        max-width: 1460px;
    }
}

.nav-menu {
    font-family: Raleway-Medium, Arial, Helvetica, sans-serif;
    display: none;

    @media (min-width:992px) {
        display: flex;
    }
}

.nav-menu__link {
    padding: 20px 15px;

    @media (min-width:1200px) {
        padding: 20px;
    }
}

.menu-btn {
    cursor: pointer;
    display: flex;
    flex-direction: column;
    padding: 7px 6px;

    & span,
    &:before,
    &:after {
        display: block;
        content: "";
        height: 2px;
        border-radius: 2px;
        background: #372e11;
    }

    & span {
        width: 13px;
        margin: 5px 0;
    }

    &:before {
        width: 18px;
    }

    &:after {
        width: 8px;
    }

    @media (min-width:576px) {
        padding: 10px 8px;

        & span {
            width: 17px;
            margin: 7px 0;
        }

        &:before {
            width: 24px;
        }

        &:after {
            width: 10px;
        }
    }

    @media (min-width:992px) {
        display: none;
    }
}

.menu-lang {
    font-size: 16px;
    line-height: 16px;
    display: flex;

    @media (min-width:576px) {
        font-size: 20px;
        line-height: 23px;
    }

    @media (min-width:992px) {
        display: none;
    }
}

.menu-lang__link {
    padding: 7px;
    color: #9b9b99;

    &:hover,
    &focus {
        color: #9b9b99;
    }
}

.menu-lang__link_active {
    color: #372e11;

    &:hover,
    &focus {
        color: #fffbed;
    }
}

.header-action {
    display: flex;
    align-items: center;

    @media (min-width:360px) {
        gap: 10px;
    }

    @media (min-width:768px) {
        gap: 20px;
    }
}

.phone {
    font-family: Roboto-Medium, Arial, Helvetica, sans-serif;
}

.header-action__phone {
    padding: 8px;

    @media (min-width:576px) {
        padding: 16px 5px;
    }

    @media (min-width:768px) {
        padding: 16px 30px;
    }
}

.header-action__link {
    display: none;

    @media (min-width:576px) {
        display: block;
        padding: 14px 20px;
    }

    @media (min-width:768px) {
        padding: 14px 30px;
    }
}

.header-action__button {
    padding: 14px 20px;
    margin-left: -10px;

    @media (min-width:1200px) {
        padding: 14px 30px;
    }
}

.button_theme_transparent {
    display: none;
    border: 1px solid #000000;

    @media (min-width:992px) {
        display: block;
    }
}

.masthead {
    position: relative;
    background-image: url("./../images/masthead_mob.jpg");
    background-position: center top;
    background-repeat: no-repeat;
    background-size: cover;
    text-align: center;
    padding: 34px 0 92px 0;

    @media (min-width:576px) {
        padding: 0 0 147px 0;
        background-image: url("./../images/masthead_tab.jpg");
    }

    @media (min-width:992px) {
        background-image: url("./../images/masthead.jpg");
    }

    @media (min-width:1200px) {
        padding: 106px 0 231px 0;
    }
}

.masthead-container {
    margin: 0 auto;
    padding: 60px 20px;

    @media (min-width:576px) {
        padding: 60px 20px;
    }

    @media (min-width:992px) {
        max-width: 887px;
        padding: 60px 0;
    }
}

.masthead-container__text {
    font-family: Raleway-Light, Arial, Helvetica, sans-serif;

    @media (min-width:576px) {
        margin: 0 auto;
        font-size: 26px;
        line-height: 31px;
    }

    @media (min-width:768px) {
        max-width: 700px;
    }
}

.masthead-action {
    display: flex;
    justify-content: center;
    gap: 8px;
    margin-top: 24px;

    @media (min-width:576px) {
        gap: 20px;
        margin-top: 60px;
    }
}

.masthead-action__button_buy {
    padding: 12px 13px;

    @media (min-width:576px) {
        padding: 22px 34px;
    }
}

.masthead-action__button_sale {
    padding: 12px 8px;

    @media (min-width:576px) {
        padding: 22px 42px;
    }
}

.order {
    padding: 20px;
    background-size: cover;
    background-color: #ffd457;
    background-image: url("./../images/order_mob.jpg");
    background-position: center top;
    background-repeat: no-repeat;

    @media (min-width:576px) {
        padding: 40px;
        background-image: url("./../images/order_tab.jpg");
    }

    @media (min-width:992px) {
        padding: 100px 80px;
        background-image: url("./../images/order.jpg");
    }

    @media (min-width:1200px) {
        padding: 200px 80px;
    }
}

.order-container {
    margin: 0 auto;
    padding: 20px;
    background-color: #fffbed;

    @media (min-width:576px) {
        padding: 32px;
    }

    @media (min-width:992px) {
        display: flex;
        justify-content: space-between;
        gap: 40px;
        padding: 80px;
        background-image: url("./../images/order-bottom.jpg");
        background-position: left bottom;
        background-repeat: no-repeat;
    }

    @media (min-width:1200px) {
        max-width: 1140px;
        gap: 80px;
    }
}

.order-text {
    padding-bottom: 32px;

    @media (min-width:576px) {
        padding-bottom: 58px;
    }

    @media (min-width:992px) {
        padding: 0;
    }
}

.order-form {
    @media (min-width:992px) {
        min-width: 320px;
    }
}

.form {
    display: flex;
    gap: 20px;
    flex-direction: column;

    @media (min-width:576px) {
        justify-content: space-between;
        flex-direction: row;
    }

    @media (min-width:768px) {
        gap: 40px;
    }

    @media (min-width:992px) {
        flex-direction: column;
        gap: 20px;
        margin: 2px 0 0 0;
    }
}

.form-block {
    gap: 20px;
    display: flex;
    flex-direction: column;

    @media (min-width:576px) {
        width: 50%;
    }

    @media (min-width:992px) {
        width: 100%;
    }
}

.form-item {
    display: flex;
    flex-direction: column;
    gap: 10px;
}

.form-item__input {
    display: block;
    font-family: Raleway-Medium, Arial, Helvetica, sans-serif;
    font-size: 14px;
    line-height: 16px;
    background-color: #fff8e1;
    border-radius: 12px;
    padding: 16px 8px;

    ::placeholder {
        color: #8e825c;
    }
}

.form-item__label {
    display: block;
    font-family: Raleway-Medium, Arial, Helvetica, sans-serif;
    cursor: pointer;
    font-size: 14px;
    line-height: 16px;
}

.form-block__form-item {
    display: flex;
    flex-direction: row;
    gap: 10px;
    margin-top: 20px;

    @media (min-width:576px) {
        margin-top: auto;
    }

    @media (min-width:992px) {
        margin-top: 20px;
    }
}

.form-item__button {
    padding: 16px;
}

.footer {
    background: #372e11;
    color: #fffbed;
    font-family: Raleway-Medium, Arial, Helvetica, sans-serif;
    padding: 32px 0;

    @media (min-width:992px) {
        padding: 80px 0;
    }
}

.footer-container {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    padding: 0 20px;
    flex-wrap: wrap;
    gap: 48px;

    @media (min-width:576px) {
        flex-direction: row;
        padding: 0 24px;
        gap: 0;
    }

    @media (min-width:1460px) {
        max-width: 1460px;
        padding: 0 20px;
        margin: 0 auto;
    }
}

.footer-wrap {
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    flex-wrap: wrap;
    gap: 48px;

    @media (min-width:992px) {
        flex-direction: row;
        gap: 230px;
    }
}

.footer__title {
    font-family: Marmelad, Arial, Helvetica, sans-serif;
    font-size: 24px;
    line-height: 29px;
    margin-bottom: 24px;
}

.footer-address {
    display: flex;
    flex-direction: column;
    gap: 16px;
    font-size: 18px;
    line-height: 21px;

    @media (min-width:992px) {
        gap: 40px;
    }

    @media (min-width:1200px) {
        flex-direction: row;
    }
}

.social {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.footer-feedback {
    @media (min-width:576px) {
        min-width: 247px;
    }

    @media (min-width:992px) {
        min-width: inherit;
    }
}

.footer-action {
    display: flex;
    flex-direction: column;
    gap: 16px;
}

.footer__mail {
    font-size: 20px;
    line-height: 23px;
    color: #fffbed;

    &:hover,
    &focus {
        color: #fffbed;
    }
}

.footer-action__button {
    display: inline;
    font-size: 16px;
    line-height: 19px;
    padding: 12px 32px;
    text-align: center;
    width: max-content;

    &:after {
        content: "";
        display: inline-block;
        vertical-align: middle;
        width: 24px;
        height: 24px;
        background: url("./../svg/mail.svg") center no-repeat;
        margin-left: 10px;
    }

    @media (min-width:576px) {
        width: 100%;
    }

    @media (min-width:992px) {
        width: max-content;
    }
}

.footer__copy {
    color: #b9b3b3;
    font-size: 12px;
    line-height: 14px;
    margin-top: 48px;
    padding: 0 20px;

    @media (min-width:576px) {
        padding: 0 24px;
    }

    @media (min-width:992px) {
        display: none;
    }
}
